.history-container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  width: 100vw;
  background-color: #ebe8e8;
  overflow: hidden;
}
.history-container .timeline-bar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: black;
  color: white;
  width: 100vw;
  height: 0%;
  padding: 1.5rem 4rem;
  margin-top: 4.5rem;
  z-index: 3;
}
.history-container .timeline-bar select {
  -webkit-appearance: none !important;
  -moz-appearance: none !important;
  appearance: none !important;
  background: none !important;
  background-color: transparent;
  border: none;
  padding: 0 1em 0 0;
  margin: 0;
  width: 100%;
  font-family: "RobotoBold";
  color: black;
  cursor: inherit;
  line-height: inherit;
  z-index: 1;
  outline: none;
}
.history-container .timeline-bar select::-ms-expand {
  display: none !important;
}
.history-container .timeline-bar .select {
  display: grid;
  grid-template-areas: "select";
  align-items: center;
  position: relative;
  width: 12%;
  border: 0.625em solid #D00019;
  padding: 0.5em 0.5em;
  font-size: clamp(var(--min), var(--val), var(--max));
  --min: 0.5rem;
  --val: 1vw;
  --max: 2rem;
  cursor: pointer;
  line-height: 1.1;
  background-color: white;
}
.history-container .timeline-bar .select select, .history-container .timeline-bar .select::after {
  grid-area: select;
}
.history-container .timeline-bar .select::after {
  content: "▼";
  justify-self: center;
  align-self: center;
  padding: 1em;
  background-color: #d00019;
  transition: 0.25s all ease;
  pointer-events: none;
  position: absolute;
  left: 80%;
}
.history-container .timeline-bar select:focus + .focus {
  position: absolute;
