#password-container {
  .entry {
    margin: unset !important;
    margin-bottom: 0rem !important;
    margin-top: 0rem !important;
    padding: unset !important;
    display: block !important;
    background: none !important;
    border-radius: unset !important;
    max-width: 100% !important;
    justify-self: center !important;
    overflow: hidden !important;
    justify-content: unset !important;
    background-image: none !important;
    .entry-header {
      display: flex !important;
      flex-direction: column !important;
      justify-content: space-between !important;
      align-items: flex-start !important;
      max-height: 100vw !important;
      width: 100% !important;
      padding: 0rem 2rem !important;
      overflow: hidden !important;
    }
    .entry-content {
      color: white !important;
      width: 100% !important;
      padding: 2rem !important;
      background-color: #272727!important;
      border-radius: 3px !important;
      margin: 2rem !important;
    }
    .entry-title {
      font-size: clamp(var(--min), var(--val), var(--max)) !important;
      --min: 1rem !important;
      --val: 2.4vw !important;
      --max: 2.8125rem !important;
      text-transform: uppercase !important;
      font-family: "RobotoBold" !important;
    }
    .entry-footer {
      display: block !important;
      padding: 0rem 2rem !important;
    }
    .um-button {
      background: #be1b17 !important;
      font-family: "RobotoBold" !important;
    }
  }
}

.um-field-block {
  color: white !important;
  font-family: "RobotoBold" !important;
  text-align: left !important;
}

.um-field-area {
  font-family: "RobotoBold" !important;
}

.um-form-field.valid {
  width: 80%!important;
}

@media screen and (min-width: 47rem) and (max-width: 64rem) and (orientation: portrait) {
  #password-container {
    .entry {
      margin: 6rem !important;

      .entry-title {
        font-size: clamp(var(--min), var(--val), var(--max)) !important;
        --min: 1rem !important;
        --val: 4.3vw !important;
        --max: 3rem !important;
      }
    }
  }
}

@media screen and (max-width: 47rem) and (orientation: portrait) {
  #password-container {
    .entry {
      margin: 3rem 0rem !important;
      .entry-title {
        font-size: clamp(var(--min), var(--val), var(--max)) !important;
        --min: 1rem !important;
        --val: 5.5vw !important;
        --max: 3rem !important;
      }
      .entry-header {
        padding: 0rem 2rem !important;
      }
      .entry-content {
        padding: 1rem 2rem !important;
        max-width: 85%!important;
        margin: 2rem auto!important;
      }
      .entry-footer {
        padding: 0rem 2rem !important;
      }
    }
  }
}
